body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

.friendCard {
    
    border-radius: 5px;
    margin: 1rem;
    position: relative;
    text-align: center;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    box-shadow: 6px 5px 5px grey;
    text-align: left;
    border: 1px solid black; 
    background-size: cover;
    width: 200px;
    height: 200px;
    
  }
  
  .friendCard > .img-container { 
    
    text-align: center;
    background:#6CADDC;
    
  } 
  .friendCard > .img-container > img {
    width: 100px;
    height: 100px;
    
  }  
  
 
  .friendCard:hover {
    border: 2px solid red;
} 
   
  
 

  

  .wrapper {
    
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;
    width: 60%;
    margin: 10% auto 0px auto;
    z-index: -1;
  }

  @media (min-width: 1100px) {
    .container {
      margin-top: 30%;
      padding-left: 100px;
      padding-right: 100px;
      width: 1100px;
    }
  }
.header {
    background: #FFDAB9;
    width: 100%;
    min-height: 80px;
    color: black;
    position: fixed;
    top: 0;
    margin: 0 auto;
    box-shadow: 0 5px 5px darkslategrey;
    z-index: 1000;
}

li {
    margin-bottom: 1%;
    text-align: center;
}

.header ul {
    list-style-type: none;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: center;
}

.header li {
    display: inline-block;
    width: 33%;
    padding-top: 20px;
    font-family: 'Chakra Petch', sans-serif;
    font-size: 30px;
}

.title {
    font: 400 100px/1.0 'Just Another Hand', Helvetica, sans-serif;
    color: #f10129;;
    text-shadow: 0px 3px 0px #660505, 0px 5px 0px #660505, 0px 7px 0px #701c1c, 0px 9px 0px #660505, 3px 8px 15px #660505, 3px 8px 5px #660505;
}


#score {
    font-size: 1.2em;
}


@media (max-width: 700px) {

    .header li {
        display: block;
        text-align: center;
        width: 100%;
        padding-top: 0;
    } 
}
.gameMessage {
    font-size: 1.5em;
    font-family: 'Ubuntu', sans-serif;
}

.black {
    color: black;
}

.correct {
    color: LAWNGREEN;
}

.incorrect {
    color: red;
    -webkit-text-stroke: 1px black;
}

.footer {
    min-height: 80px;
    width: 100%;
    background: #FFDAB9;
    box-shadow: 0 0 5px 1px darkslategrey;
    position: relative;
    margin-top: 50px;
    margin: 0 auto;
   
}

.bottom {
    position: absolute;
    bottom: 0;
    margin: 0 auto;
    width: 100%;
    min-height: 50px;
    border-top: 5px solid #FFDAB9;
    background: #FFDAB9;
    text-align: center;
}





@media (max-width: 600px) {

    .bottom {
        bottom: -30px;
    }

    #footer-list li {
        width: 100%;
        display: block;
        padding-bottom: 5px;
    }
}

@media (min-height: 860px) and (min-width: 1000px) {

    .footer {
        position: absolute;
        bottom: 0;
    }   
}
